import download from 'downloadjs';
import store from "../store";
import axios from "axios";
import Moment from "moment";

const getLibraryData = (loadinText) => {
  loadinText && store.app.showLoader(loadinText);
  axios.get(`${process.env.VUE_APP_API}/libraryBasicData`).then((response) => {
    store.mainLayout.state.libraryBasicDetailsLoaded = true;
    store.mainLayout.state.libraryName = response.data.name;
    store.mainLayout.state.libraryPhone = response.data.phone;
    store.mainLayout.state.libraryEmail = response.data.email;
    store.mainLayout.state.libraryAddress = response.data.address;
    store.mainLayout.state.libraryHasLogo = response.data.hasLogo;
    store.mainLayout.state.bUploadInProgress = response.data?.settings?.bUploadInProgress || null;
    store.mainLayout.state.libraryLogoUrl = `${process.env.VUE_APP_PUBLIC_BUCKET}/library-logos/${response.data._id}_thumb.jpg?u=${response.data.updatedAt}`;
  }).finally(() => {
    loadinText && store.app.hideLoader();
  });
};

const vueBarEvents = (ele, events) => {
  const scrollPane = ele.querySelector(".vb-content");
  if (events.onScroll) {
    scrollPane.addEventListener("scroll", events.onScroll);
  }
  if (events.onScrollToBottom) {
    scrollPane.addEventListener("scroll", () => {
      const offset = events.scrollToBottomOffset
        ? events.scrollToBottomOffset
        : 0;
      if (
        scrollPane.scrollHeight - scrollPane.offsetHeight - offset <=
        Math.ceil(scrollPane.scrollTop)
      ) {
        events.onScrollToBottom();
      }
    });
  }
};

const convertToIst = (currentTime) => {
  const currentOffset = currentTime.getTimezoneOffset();

  const ISTOffset = 330; // IST offset UTC +5:30

  const ISTTime = new Date(
    currentTime.getTime() + (ISTOffset + currentOffset) * 60000
  );

  return ISTTime;
};

function arrDiff(a1, a2) {
  var a = [],
    diff = [];

  for (var i = 0; i < a1.length; i++) {
    a[a1[i]] = true;
  }

  for (var i = 0; i < a2.length; i++) {
    if (a[a2[i]]) {
      delete a[a2[i]];
    } else {
      a[a2[i]] = true;
    }
  }

  for (var k in a) {
    diff.push(k);
  }

  return diff;
}

function monthName(mon) {
  return [
    "Jan",
    "Feb",
    "Mar",
    "Apr",
    "May",
    "Jun",
    "Jul",
    "Aug",
    "Sep",
    "Oct",
    "Nov",
    "Dec"
  ][mon - 1];
}

function dataURItoBlob(dataURI) {
  var byteString = atob(dataURI.split(',')[1]);
  var mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0]
  var ab = new ArrayBuffer(byteString.length);
  var ia = new Uint8Array(ab);
  for (var i = 0; i < byteString.length; i++) {
    ia[i] = byteString.charCodeAt(i);
  }
  var blob = new Blob([ab], { type: mimeString });
  return blob;
}

function downloadFromDataUri(dataUri, fileName, mimeType) {
  download(dataURItoBlob(dataUri), fileName, mimeType);
}

function downloadFromBlob(blob, fileName, mimeType) {
  download(blob, fileName, mimeType);
}

function vueAddTimeFomats(Vue) {
  Vue.filter("formatDate", function (value) {
    if (value) {
      return Moment(value).format("DD-MM-YYYY");
    }
  });

  Vue.filter("formatDateMonthAndYear", function (value) {
    if (value) {
      return Moment(value).format("MMM, YYYY");
    }
  });

  Vue.filter("formatTime", function (value) {
    if (value) {
      return Moment(value).format("hh:mm A");
    }
  });

  Vue.filter("formatDateTime", function (value) {
    if (value) {
      return Moment(value).format("DD-MM-YYYY [at] hh:mm A");
    }
  });
}

const setWindowValues = (values) => {
  for (const key in values) {
    window[key] = values[key];
  }
}

const getBase64FromFile = (file, onLoad, Onerror) => {
  var reader = new FileReader();
  reader.readAsDataURL(file);
  onLoad && (reader.onload = () => onLoad(reader.result));
  Onerror && (reader.onerror = Onerror);
}

export default {
  vueBarEvents,
  vueAddTimeFomats,
  convertToIst,
  arrDiff,
  monthName,
  downloadFromDataUri,
  downloadFromBlob,
  getLibraryData,
  setWindowValues,
  getBase64FromFile
};