import Vue from "vue";
import VueRouter from "vue-router";
import { lazy } from "vue-async-manager";
import routeChangeEvents from "./routeChangeEvents";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "dashboard",
    meta: {
      bigHeader: true,
      title: "Dashboard"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "dashboard" */ "../views/Dashboard.vue")
    )
  },
  {
    path: "/books",
    name: "books",
    meta: {
      title: "Books"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "books" */ "../views/Books.vue")
    )
  },
  {
    path: "/logs",
    name: "logs",
    meta: {
      title: "Logs"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "logs" */ "../views/Logs.vue")
    )
  },
  {
    path: "/reports",
    name: "reports",
    meta: {
      title: "Reports"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "reports" */ "../views/Reports.vue")
    )
  },
  {
    path: "/books/addNew",
    name: "new_book",
    meta: {
      title: "Add new book"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "singleBook" */ "../views/SingleBook.vue")
    )
  },
  {
    path: "/books/:id",
    name: "single_book",
    meta: {
      title: "Book details"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "singleBook" */ "../views/SingleBook.vue")
    )
  },
  {
    path: "/distribute",
    name: "distribute",
    meta: {
      title: "Distribute"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "distribute" */ "../views/Distribute.vue")
    )
  },
  {
    path: "/return",
    name: "returnBook",
    meta: {
      title: "Return"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "return" */ "../views/Return.vue")
    )
  },
  {
    path: "/members",
    name: "members",
    meta: {
      title: "Members"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "members" */ "../views/Members.vue")
    )
  },
  {
    path: "/members/addNew",
    name: "new_member",
    meta: {
      title: "Add member details"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "singleMember" */ "../views/SingleMember.vue")
    )
  },
  {
    path: "/members/:id",
    name: "single_member",
    meta: {
      title: "Member details"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "singleMember" */ "../views/SingleMember.vue")
    )
  },
  {
    path: "/bulkUpload",
    name: "bulk_upload",
    meta: {
      title: "Bulk Upload"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "bulkUpload" */ "../views/BulkUpload.vue")
    )
  },
  {
    path: "/settings",
    name: "settings",
    meta: {
      title: "Settings"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "settings" */ "../views/Settings.vue")
    )
  },
  {
    path: "*",
    name: "404_not_found",
    meta: {
      title: "Page not found"
    },
    component: lazy(() =>
      import(/* webpackChunkName: "notFound" */ "../views/NotFound.vue")
    )
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  routeChangeEvents(to, from);
  next();
});

export default router;
