import { ref, computed, toRefs } from "@vue/composition-api";
import store from "../store";
import axios from "axios";
import Moment from "moment";

const state = store.dashboard.state;

const loadData = () => {
  axios
    .get(`${process.env.VUE_APP_API}/libraryDashboardData`)
    .then((response) => {
      state.bookCard = response.data.bookCard;
      state.dueCard = response.data.dueCard;
      state.graphCard = response.data.graphCard;
    })
    .catch((error) => {
      return Promise.reject(error);
    })
    .finally(() => {
      state.loading = false;
    });
};

const series = computed(() => {
  return [
    {
      name: "Books Distributed",
      data: state.graphCard ? state.graphCard.map((data) => data.value) : []
    }
  ];
});

const monthNames = computed(() => {
  return state.graphCard ? state.graphCard.map((data) => Moment(data.monthString, "MMM").format("MMMM")) : [];
});

export default (props, { root }) => {
  loadData();
  return {
    ...toRefs(state),
    series,
    monthNames
  };
};
