import { ref, computed, toRefs } from "@vue/composition-api";
import store from "../store";
import axios from "axios";
import helpers from "../helpers";

const usernameError = ref("");
const passwordError = ref("");
const showEye = ref(false);

const width = computed(() => {
  switch (store.app.getScreenSize) {
    case "xs":
      return 320;
    case "sm":
      return 500;
    case "md":
      return 500;
    case "lg":
      return 550;
    case "xl":
      return 550;
  }
});

const attemptLogin = () => {
  const userName = store.loginLayout.state.userName;
  const password = store.loginLayout.state.password;
  const keepSignedIn = store.loginLayout.state.keepSignedIn;
  store.loginLayout.state.loading = true;
  axios
    .post(`${process.env.VUE_APP_API_NOAUTH}/accesstoken`, {
      username: userName,
      password: password,
      grant_type: "password",
      keep_signed_in: keepSignedIn,
      set_cookie: true
    })
    .then((response) => {
      store.loginLayout.state.userName = store.loginLayout.state.password = "";
      store.app.logIn();
      helpers.localStorage.setItem("logged_in", true);
      helpers.localStorage.setItem("keep_signed_in", keepSignedIn);
    })
    .catch(({ response }) => {
      if (response?.data?.feild == "password") {
        passwordError.value = response.data.message;
      }
      else if (response?.data?.feild == "username") {
        usernameError.value = response.data.message;
      }
      else {
        store.app.showAlert("Some error occured!");
      }
    })
    .finally(() => {
      store.loginLayout.state.loading = false;
    });
};

export default (props, { root }) => {
  const form = ref(null);
  const usernameInput = ref(null);
  const passwordInput = ref(null);
  const state = store.loginLayout.state;

  const validate = () => {
    if (form.value.validate()) {
      attemptLogin();
    }
  };

  const nameRules = [
    (v) =>
      !!v ||
      (usernameError.value.length
        ? usernameError.value
        : "Username is required")
  ];
  const passwordRules = [
    (v) =>
      !!v ||
      (passwordError.value.length
        ? passwordError.value
        : "Password is required")
  ];
  setTimeout(() => {
    usernameInput.value.$el.querySelector("input").focus();
  }, 0);

  return {
    ...toRefs(state),
    valid: true,
    nameRules,
    passwordRules,
    form,
    usernameInput,
    passwordInput,
    validate,
    usernameError,
    passwordError,
    attemptLogin,
    showEye,
    width
  };
};
